.Story{
    background: #969696;
    border-radius: 100%;
    height: 56px;
    width: 56px;
    flex-direction: column;
    margin: 1px 8px 0 8px;
    cursor: pointer;
    transition: all 0.3s;
    
}
.Story :hover{
opacity: 0.6;
}

.Story img {
    height: 56px;
    width: 56px;
    border-radius: 100%;
}

.Story .nomeStory{
    text-align: center;
    font-size: 13px;

}